.portfolio {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 1100px;
  margin: 20px auto 0;
  padding: 0 30px;

  &__website {
    position: relative;

    margin: 3px;
  }
}

.website-link {
  display: inline-block;

  border-radius: 5px;

  transition: box-shadow 0.2s;

  &:hover {
    box-shadow: 0 0 20px #000000;

    .website__title {
      background: linear-gradient(to right, #f12711, #ea9a00);
    }
  }
}

.website {
  width: 250px;
  height: 154px;
  overflow: hidden;

  border-radius: 5px;
  box-shadow: 0 0 12px #555555;

  &__thumb {
    width: 100%;
    height: auto;
  }

  &__title {
    position: absolute;
    bottom: 2px;
    left: 0;

    display: block;
    padding: 0 7px;

    font-weight: 300;
    font-size: 1rem;
    font-family: "Open Sans", "Helvetica", "Arial", sans-serif;
    color: #f9f9f9;

    background: linear-gradient(to right, #160f8c, #2196f3);
    border-radius: 0 2px 2px 0;
    opacity: 0.9;
  }
}
