.intro {
  max-width: 1100px;
  margin: 0 auto;
  padding: 0 30px;

  text-align: center;

  &__author-img {
    width: 46px;
    height: 46px;

    border-radius: 7px;
  }

  &__header {
    margin-top: 8px;
    margin-bottom: 14px;

    font-weight: 300;
    font-size: 1.5rem;
    line-height: 1.3;
    font-family: "Open Sans", "Helvetica", "Arial", sans-serif;
    text-align: center;
    color: $font-white;
    text-shadow: 0 0 1px #444444;
  }
}

.socials {
  display: flex;
  justify-content: center;

  &__link-github {
    margin-right: 7px;
  }
}

.social-icon {
  display: block;
  width: 1.25rem;
  height: 1.25rem;

  transition: fill 0.2s;

  fill: $icon;

  &:hover {
    transition: fill 0.2s;

    fill: $icon-hover;
  }
}
